import React, { useEffect } from 'react'
import { useAlert } from 'react-alert'
import { useDispatch, useSelector } from 'react-redux'
import { useParams } from 'react-router-dom'
import { getProductDetails, clearErrors } from '../../actions/productActions'
import Loader from '../layouts/Loader'
import MetaData from '../layouts/MetaData'
import { Carousel } from 'react-responsive-carousel'
import 'react-responsive-carousel/lib/styles/carousel.min.css';

const ProductDetails = () => {
    const dispatch = useDispatch();
    const alert = useAlert();
    const { id } = useParams();
    const { loading, error, product } = useSelector(
        state => state.productDetails
    );
    useEffect(() => {
        dispatch(getProductDetails(id))
        if (error) {
            alert.error(error)
            dispatch(clearErrors())
        }
    }, [dispatch, alert, error, id])
    return (
        <>
            <section class="section">
                <div class="container">
                    {loading ? <Loader /> : (
                        <>
                            <MetaData title={product.name} />
                            <div class="columns is-vcentered">
                                <div className='row f-flex justify-content-around'>
                                    <Carousel>
                                        {product.images &&
                                            product.images.map((image) => (
                                                <div key={image.public_id}>
                                                    <img
                                                        className='d-block w-100'
                                                        src={image.url}
                                                        alt={product.title}
                                                    />
                                                </div>
                                            ))}
                                    </Carousel>
                                </div>
                                <div class="column is-7">
                                    <div class="columns">
                                        <div class="column is-two-thirds">
                                            <h1 class="title is-5">{product.name}</h1>
                                            <p class="subtitle is-6">{product.seller}</p>
                                        </div>
                                        <div class="column">
                                            <div class="content">
                                                <p class="has-text-weight-bold">Price:</p>
                                                <p class="is-size-3">₹ {product.price}</p>
                                            </div>
                                        </div>
                                    </div>
                                    <hr />
                                    <h2 class="subtitle is-4">Product Description</h2>
                                    <p class="subtitle is-6"><b>item # : {product._id}</b></p>
                                    <p>{product.description}</p>
                                    <br />
                                    <br />
                                    <div class="rating">
                                        <b>{product.ratings ? product.ratings : "0"}<span class="icon has-text-warning"><i class="fas fa-star"></i></span> rating</b>
                                    </div>
                                    <hr />
                                    <div class="field is-grouped has-addons has-addons-centered">
                                        <div class="control">
                                            <button class="button is-danger" id="quantity-decrease">-</button>
                                        </div>
                                        <div class="control">
                                            <input class="input is-small" type="number" />
                                        </div>
                                        <div class="control">
                                            <button class="button is-primary" id="quantity-increase">+</button>
                                        </div>
                                    </div>
                                    <p>
                                    </p>
                                    <p class="subtitle is-5 is-marginless ml-4" id="stock">Stock count: {product.stock === 0 ? 'Out of stock' : product.stock}</p>
                                    <hr />
                                    <div class="field has-text-centered">
                                        <div class="control">
                                            <button class="button is-primary">Add to Cart</button>
                                        </div>
                                    </div>
                                    <hr />

                                </div>
                            </div>
                            <div className="review-field">
                                <div className="field">
                                    <label className="label">Leave a Review</label>
                                    <div className="control">
                                        <textarea className="textarea" placeholder="Write your review here..."></textarea>
                                    </div>
                                </div>
                                <div className="field">
                                    <label className="label">Rating</label>
                                    <div className="control">
                                        <div className="select">
                                            <select>
                                                <option>Select rating</option>
                                                <option>1</option>
                                                <option>2</option>
                                                <option>3</option>
                                                <option>4</option>
                                                <option>5</option>
                                            </select>
                                        </div>
                                    </div>
                                </div>
                                <div className="field">
                                    <div className="control">
                                        <button className="button is-primary">Submit Review</button>
                                    </div>
                                </div>
                                <div class="existing-reviews has-text-left">
                                    <h2 class="title is-5">Customer Reviews</h2>
                                    {(!product.reviews || !product.reviews.length) ? (
                                        <p>No reviews yet...</p>
                                    ) : (
                                        product.reviews.map((review) => (
                                            <>
                                                <div className="review" key={review.id}>
                                                    <p className="review-name has-text-weight-bold">{review.name}</p>
                                                    <div className="review-body">
                                                        <p className="review-text">{review.comment}</p>
                                                        <p className="review-rating has-text-weight-bold">Rating: {review.rating} <span className="icon has-text-warning"><i className="fas fa-star"></i></span></p>
                                                    </div>
                                                </div>
                                                <br />
                                            </>
                                        ))
                                    )}
                                </div>
                            </div>
                        </>
                    )}
                </div>
            </section>
        </>
    )
}

export default ProductDetails
