import React, { useState, useEffect } from 'react'
import MetaData from './layouts/MetaData'
import { useDispatch, useSelector } from 'react-redux'
import { getProducts } from '../actions/productActions'
import Product from './product/Product'
import Loader from './layouts/Loader'
import { useAlert } from 'react-alert'
import Pagination from 'rc-pagination';
import 'rc-pagination/assets/index.css';
import { useParams } from 'react-router-dom'

const Home = () => {
    const [currentPage, setCurrentPage] = useState(1)
    const dispatch = useDispatch()
    const alert = useAlert()
    let params = useParams()
    const { loading, products, error, productCount, resultPerPage } = useSelector(state => state.products)
    const keyword = params.keyword
    useEffect(() => {
        if (error) {
            alert.success('Success')
            return alert.error(error)
        }
        dispatch(getProducts(keyword, currentPage))
    }, [dispatch, alert, error, keyword, currentPage])

    function setCurrentPageNo(pageNumber) {
        setCurrentPage(pageNumber)
    }
    return (
        <>
            {loading ? <Loader /> : (
                <>
                    <MetaData title={`Buy products at low cost`} />
                    <section id="products" className="container mt-5">
                        <u style={{ color: "#d9ac7c" }}><span className="logo-text has-text-weight-bold is-size-3 is-family-serif" style={{ color: "#d9ac7c" }}>Latest products </span></u>
                        <div className="columns is-multiline is-flex-mobile">
                            {products && products.map(product => (
                                <Product key={product._id} product={product} />
                            ))}
                        </div>
                    </section>

                    {resultPerPage <= productCount && (
                        <><div className='pagination is-centered mt-6' style={{ display: "flex", justifyContent: "center" }}>
                            <Pagination
                                current={currentPage}
                                total={productCount}
                                pageSize={resultPerPage}
                                onChange={setCurrentPageNo}
                                locale={{
                                    prev_page_text: '',
                                    next_page_text: ''
                                }}
                            />
                        </div>
                            <br />
                        </>
                    )}
                </>
            )}
        </>
    )
}

export default Home
